import { useEffect } from 'react';
import { paramCase } from 'change-case';
import { useParams, useLocation } from 'react-router-dom';
// @mui
import { Container } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getProducts } from '../../redux/slices/product';
// routes
import { PATH_APPLICATION } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import ProductNewForm from '../../sections/@dashboard/user/product/ProductNewForm';

// ----------------------------------------------------------------------

export default function UserCreateProduct() {
  const { themeStretch } = useSettings();
  const dispatch = useDispatch();
  const { pathname } = useLocation();
  const {id} = useParams();
  const { products } = useSelector((state) => state.product);
  
  const currentProduct = id && products.find((product) => { return (product._id === id)});
 
  const isEdit = ((id && currentProduct && currentProduct._id) && true || false);
   
  return (
    <Page title="Ecommerce: Create a new product">
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <HeaderBreadcrumbs
          sx = {{mb:0}}
          heading="Account"
          links={[
            { name: 'Profile', href: PATH_APPLICATION.profile.account },
            { name: 'Product List', href: PATH_APPLICATION.profile.product },
            { name: 'Product Upload' },
          ]}
        />
        <ProductNewForm isEdit={isEdit} currentProduct={currentProduct} />
      </Container>
    </Page>
  );
}
